import { Component, OnInit } from '@angular/core';
import { Validators, FormControl, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { SetItemService } from 'src/app/services/set-item.service';
import { ApiService } from 'src/app/services/api.service';
import { ToasterService } from 'src/app/toaster.service';

@Component({
    selector: 'app-additem',
    templateUrl: './additem.component.html',
    styleUrls: ['./additem.component.css'],
})
export class AdditemComponent implements OnInit {
    heading = 'Add Product';
    editMode = false;
    // post:any;
    images;
    newitem: FormGroup;
    Genders = ['Male', 'Female', 'Others'];
    data: any;
    itemData: any;
    constructor(
        private setitem: SetItemService,
        private route: ActivatedRoute,
        private router: Router,
        private apiService: ApiService,
        private toasterService: ToasterService
    ) {
        this.editMode = this.route.snapshot.queryParams._id ? true : false;
        console.log('this is items', this.setitem.post);
        this.itemData = this.setitem.post.filter((res) => {
            return res;
        });
    }
    show: any;
    showButton = true;
    showdropdown = false;

    ngOnInit(): void {
        this.newitem = new FormGroup({
            productName: new FormControl(null, [Validators.required]),
            Description: new FormControl(null, Validators.required),
            Price: new FormControl(null, Validators.required),
            size: new FormControl(null, Validators.required),
            Quantity: new FormControl(null, Validators.required),
            gender: new FormControl(null, Validators.required),
            file: new FormControl(null, Validators.required),
            suplier: new FormControl(null, Validators.required),
            category: new FormControl(null, Validators.required),
            subCategory: new FormControl(null, Validators.required),
        });
        if (this.editMode) {
            this.populateData();
        }
    }
    populateData(): void {
        this.data = JSON.parse(localStorage.getItem('edit'));
        this.newitem.patchValue({
            Productname: this.data.Productname,
            Description: this.data.Description,
            Gender: this.data.Gender,
            Quantity: this.data.Quantity,
            Size: this.data.Size,
            Price: this.data.Price,
            Suplier: this.data.Suplier,
            Category: this.data.Category,
            Subcategory: this.data.Subcategory,
        });
    }
    onsubmit(e): void {
        if (this.editMode) {
        } else {
            const formData = new FormData();
            formData.append(
                'productName',
                this.newitem.get('productName').value
            );
            formData.append(
                'Description',
                this.newitem.get('Description').value
            );
            formData.append('size', this.newitem.get('size').value);
            formData.append('quantity', this.newitem.get('Quantity').value);
            formData.append('Price', this.newitem.get('Price').value);
            formData.append('gender', this.newitem.get('gender').value);
            formData.append('suplier', this.newitem.get('suplier').value);
            formData.append('category', this.newitem.get('category').value);
            formData.append(
                'subCategory',
                this.newitem.get('subCategory').value
            );
            formData.append('file', this.images);
            console.log(formData);
            if (this.newitem.valid) {
                this.apiService.request('uploadItem', formData);
            } else {
                this.toasterService.showToaster('Form is not filled');
            }
        }
    }

    selectImages(event): void {
        if (event.target.files.length > 0) {
            const files = event.target.files[0];
            this.images = files;
            console.log(this.images);
        }
        console.log(event);
    }
    clear(): void {
        localStorage.removeItem('elementIndex');
        this.router.navigate(['admin']);
    }
}
