import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { SetItemService } from 'src/app/services/set-item.service';
import { Router, ActivatedRoute } from '@angular/router';
import * as _ from 'lodash';
import $ from 'jquery';
import { ApiService } from 'src/app/services/api.service';
@Component({
    selector: 'app-table',
    templateUrl: './table.component.html',
    styleUrls: ['./table.component.css'],
})
export class TableComponent implements OnInit {
    id: any;
    custompageindex = 0;
    nAcending = true;
    nDecending = false;
    pAcending = true;
    pDecending = false;
    cAcending = true;
    cDecending = false;
    sAcending = true;
    sDecending = false;
    qAcending = true;
    qDecending = false;
    subAcending = true;
    subDecending = false;

    constructor(
        private setitem: SetItemService,
        private route: Router,
        private activated: ActivatedRoute,
        private apiService: ApiService
    ) {}

    displayedColumns: string[] = [
        'productName',
        'Description',
        'Price',
        'suplier',
        'category',
        'size',
        'quantity',
        'subCategory',
        'file',
        'edit',
        'delete',
    ];
    dataSource = new MatTableDataSource(this.setitem.post);

    @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;

    async ngOnInit(): Promise<void> {
        const gap: any = await this.apiService.request('getItem');

        console.log();

        this.dataSource = new MatTableDataSource(gap);
        let variable = 0;
        if (localStorage.getItem('elementIndex') !== null) {
            variable = parseInt(localStorage.getItem('elementIndex'), 0);
            variable--;
        }
        this.paginator.pageIndex = Math.floor(variable / 10);
        this.dataSource.paginator = this.paginator;
        setTimeout(() => {
            if (variable > 1) {
                $('html, body').animate(
                    {
                        scrollTop: $('#row_' + variable++).offset().top,
                    },
                    1000
                );
            }
        }, 500);
    }

    applyFilter(filterValue: string): void {
        filterValue = filterValue.trim();
        filterValue = filterValue.toLowerCase();
        this.dataSource.filter = filterValue;
    }

    edit(element, i): void {
        console.log(element);
        this.id = element.id;
        localStorage.setItem('edit', JSON.stringify(element));
        localStorage.setItem('elementIndex', this.id);
        this.route.navigate(['/admin/edit-item'], {
            queryParams: { _id: this.id },
        });
    }

    fucn(element, i): void {
        const v = confirm('Are u sure?');
        if (v === true) {
            this.setitem.delete(element, i);
            this.dataSource = new MatTableDataSource(this.setitem.post);
            this.dataSource.paginator = this.paginator;
        }
    }

    ascendingOrder(option): void {
        if (option === 'name') {
            const arr = _.sortBy(this.setitem.post, [(o) => o.Productname]);
            this.dataSource = new MatTableDataSource(arr);
            this.nAcending = false;
            this.nDecending = true;
        } else if (option === 'Price') {
            const arr = this.setitem.post.sort((a, b) => a.Price - b.Price);
            this.dataSource = new MatTableDataSource(arr);
            this.pAcending = false;
            this.pDecending = true;
        } else if (option === 'category') {
            const arr = _.sortBy(this.setitem.post, [(o) => o.Category]);
            this.dataSource = new MatTableDataSource(arr);
            this.cDecending = true;
            this.cAcending = false;
        } else if (option === 'size') {
            const arr = _.sortBy(this.setitem.post, [(o) => o.size]);
            this.dataSource = new MatTableDataSource(arr);
            this.sAcending = false;
            this.sDecending = true;
        } else if (option === 'quantity') {
            const arr = this.setitem.post.sort(
                (a, b) => a.Quantity - b.Quantity
            );
            this.dataSource = new MatTableDataSource(arr);
            this.qAcending = false;
            this.qDecending = true;
        } else if (option === 'subcategory') {
            const arr = _.sortBy(this.setitem.post, [(o) => o.Subcategory]);
            this.dataSource = new MatTableDataSource(arr);
            this.subAcending = false;
            this.subDecending = true;
        }
        this.dataSource.paginator = this.paginator;
    }
    descendingOrder(option): void {
        if (option === 'name') {
            const arr = _.sortBy(this.setitem.post, [(o) => o.Productname]);
            arr.reverse();
            this.dataSource = new MatTableDataSource(arr);
            this.nAcending = true;
            this.nDecending = false;
        } else if (option === 'Price') {
            const arr = this.setitem.post.sort((a, b) => b.Price - a.Price);
            arr.reverse();
            this.dataSource = new MatTableDataSource(arr);
            this.pAcending = true;
            this.pDecending = false;
        } else if (option === 'category') {
            const arr = _.sortBy(this.setitem.post, [(o) => o.Category]);
            arr.reverse();
            this.dataSource = new MatTableDataSource(arr);
            this.cDecending = false;
            this.cAcending = true;
        } else if (option === 'size') {
            const arr = _.sortBy(this.setitem.post, [(o) => o.size]);
            arr.reverse();
            this.dataSource = new MatTableDataSource(arr);
            this.sAcending = true;
            this.sDecending = false;
        } else if (option === 'quantity') {
            const arr = this.setitem.post.sort(
                (a, b) => a.Quantity - b.Quantity
            );
            arr.reverse();
            this.dataSource = new MatTableDataSource(arr);
            this.qAcending = true;
            this.qDecending = false;
        } else if (option === 'subcategory') {
            const arr = _.sortBy(this.setitem.post, [(o) => o.Subcategory]);
            arr.reverse();
            this.dataSource = new MatTableDataSource(arr);
            this.subAcending = true;
            this.subDecending = false;
        }
        this.dataSource.paginator = this.paginator;
    }
}
