import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ApiService } from './api.service';
import { Observable } from 'rxjs';

@Injectable({
    providedIn: 'root',
})
export class SetItemService {
    post = JSON.parse(localStorage.getItem('items'));
    url = 'http://localhost:3000/';
    constructor(private http: HttpClient, private apiservice: ApiService) {}

    getdata(): Observable<any> {
        return this.http.get(`${this.url}getitem`);
    }

    delete(ele, i): void {
        const newPost = JSON.parse(localStorage.getItem('items'));
        const setPost: any = newPost.splice(i, 1);
        this.post = newPost;
        localStorage.setItem('items', JSON.stringify(this.post));
    }

    addItem(data): void {
        data.id = this.post.length++;
        if (data !== null) {
            this.post.push(data);
            localStorage.setItem('items', JSON.stringify(this.post));
            localStorage.setItem('elementIndex', data.id);
        }
    }

    clearStorage(): void {
        localStorage.clear();
    }
}
